html, * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
display: flex;
color: black;
font-family: 'Fira Sans', sans-serif;
}
.main {
    margin-top: 160px;
    padding: 0 ;

}

.nav {
    box-shadow: 0 1px 13px 2px rgba(5, 5, 5, 0.603);
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    background-color: #e3e3e3;
    top: 0;
    z-index: 999;
    
    &__items {
        display: flex;
        
    }
    &__img {
        width: 10%;
        opacity: 0.6;
        transition: opacity 500ms ease-in;
        &:hover {
         cursor: pointer;
         opacity: 1;
        }
    }


   &__item {
        border: 2px solid black;
        padding: 6px;
        background-color: white;
        margin: 10px;
        list-style-type: none;
        transition: color 300ms ease-in, background-color 300ms ease-in-out;
        &:hover {
            background-color: black;
            color: white;
            cursor: pointer;
            text-underline-position: below;
        }
    }
}
.hero {
    font-size: 25px;
    text-align: center;
    color:black;
    display: grid;
    padding: 0;
    grid-template-columns: auto auto;
    grid-template-rows:auto auto;
      .firstbox {
        grid-column-start: 1;
        padding: 20px;
    }
      .secondbox {
          padding: 20px;
        grid-row-start: 2;
        grid-column-start: 2;
    }
}
 
.hero::after {
        
        background: url(foty/5.jpeg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        content: "";
        opacity: 0.4;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: -3;   
    }

.info {
    padding:0 200px;
    margin: 200px 0;
    width: 100%;
    background-image: linear-gradient(dodgerblue, rgba(130, 130, 189, 0.507), white);
        padding:15px;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: repeat(1, 1fr);
    gap: 20px;

    &__one {
        align-self: center;
        text-align: start;
        grid-row-start: 1;
        padding: 15px;
        grid-column-start: 2;

    }
    &__two {
        align-self: center;
        margin-top: 30px;
        text-align: end;
        padding-inline-start: 15px;
        grid-row-start: 2;
        border: none;
    }
    img {
        max-width: 60%;
        min-width: 50%;
        grid-row-start: 1;
        grid-column-start: 1;
        margin: auto;
        border: none;

    }
    #proc {
        grid-row-start: 2;
        grid-column-start: 2;
    }
}

.projects {
    border: 3px solid black;
    display: flex;
    margin: 30px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    .card {
        background-image: linear-gradient(rgba(75, 107, 196, 0.856), rgba(94, 130, 139, 0.445));
        margin:20px 5px;
        opacity: 0.6;
        border: none;
        text-align: center;
        padding: 15px;
        display: flex;
        flex-direction: column;
        width: 20%;
        transition: box-shadow 500ms ease-in-out, transform 500ms ease-in-out, border 200ms ease-in-out, opacity 500ms ease-in-out;
        &:hover {
            background-color: white;
            cursor: pointer;
            box-shadow: 0 0 42px 16px black;
            transform: rotateY(180deg) scale(1.2);
            opacity: 1;
            z-index: 23;
            ;

        }
        img {
            opacity: 0.7;
        }
        .card-body {
            margin-top: 10px;
        }
    }
}


.footer {
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: auto;
}
.foo {
    height: 100px;
    display: flex;
    list-style-type: none;
    background-color: dodgerblue;
    justify-content: space-around;
    align-items: center;
}